import React from 'react';
import Typography from '@material-ui/core/Typography';
import { makeStyles } from '@material-ui/core/styles';
import Paper from '@material-ui/core/Paper';
import Layout from 'layout/Layout';
import Metadata from 'components/Metadata';
import { PrivacyPageMetadata } from 'configs/PageMetadata';

const useStyles = makeStyles(() => ({
  root: {
    margin: 20,
    padding: 20
  },
  sectionTitle: {
    paddingBottom: 10,
    fontSize: '1.20rem'
  }
}));

export default function PrivacyPage() {
  const classes = useStyles();
  return (
    <Layout>
      <Paper elevation={3} className={classes.root} >
        <Typography variant="h6" className={classes.sectionTitle}>
          Information Collection and Use:
      </Typography>
        <Typography paragraph>
          Bipul's Web aka this website, collects registration, circulation qualification, and other information (including e-mail addresses) that you provide to us. This is done to help us provide our customers with the best customer service and valuable information regarding relevant products and services from Bipul's Web and appropriate third parties.
        </Typography>
      </Paper>
      <Paper elevation={3} className={classes.root} >
        <Typography variant="h6" className={classes.sectionTitle}>
          Information Sharing and Disclosure:
        </Typography>
        <Typography paragraph>
          Data may be used to update and improve our publications; as well as inform you of important industry news, events, services, and/or products. Occasionally, we may release the data that you provide to third parties that wish to market products/services that may be of interest to you. We have procedures in place to safeguard and help prevent unauthorized access, data security, and correct use of the information we collect on-line. We will not sell, share, or rent any financial information collected from you, except as necessary to fulfill your order. The views expressed in any Bipul's Web communication channel (example: portal, blog, newsletter, social sites), are those of the individual author, and not to be attributed to Bipul's Web, or of any person or organization affiliated with or otherwise doing business with Bipul's Web.
        </Typography>
      </Paper>
      <Paper elevation={3} className={classes.root} >
        <Typography variant="h6" className={classes.sectionTitle}>
          Cookies:
        </Typography>
        <Typography paragraph>
          Bipul's Web employs cookies to recognize registered users and their access privileges, as well as to track site usage and maintain security of the userâ€™s account. In some cases, subscribers who do not accept cookies from Bipul's Web may not be able to access advanced areas of publication sites. Additionally, third parties including 'Google Inc.' may collect information through the use of cookies and web beacons.
        </Typography>
      </Paper>
      <Paper elevation={3} className={classes.root} >
        <Typography variant="h6" className={classes.sectionTitle}>
          Opt-out features:
        </Typography>
        <Typography paragraph>
          You will have opportunities to opt-out of receiving information from third parties not affiliated with Bipul's Web. Any communication that is sent to you will provide the opportunity to opt-out of future communications of the same type. Your information will also be used to send renewal requests from Bipul's Web publications and products to which you currently subscribe. To opt-out, please contact Bipul's Web in writing and let us know your specific opt-out request and it will be fulfilled as soon as possible. If you need assistance in updating the information you have provided to Bipul's Web or you have questions or comments about these policies, contact us at email above. Bipul's Web will review this privacy policy as needed and may update it periodically. By continued use of Bipul's Web content and products, you consent to the collection and use of your information by Bipul's Web and accept any changes to this policy.
        </Typography>
      </Paper>
      <Metadata
        Title={PrivacyPageMetadata.Title}
        Description={PrivacyPageMetadata.Description}
      />
    </Layout>
  );
}